.main {
    background-color: #060606;
    display: flex;
    flex-direction: column;
    justify-content: center;  /* 수직 중앙 정렬 */
    align-items: center;      /* 수평 중앙 정렬 */
    min-height: 100vh;        /* 화면 높이를 채우도록 설정 */
    padding: 20px;
    color: white;
  /* 자식 요소를 절대 위치로 설정할 수 있게 해줌 */
}

.title {
    text-align: center;
    font-size: 60px;
    font-weight: bold;
    margin-bottom: 20px;
}

.text {
    display: flex;
    justify-content: flex-start;
    font-size: 20px;
    margin: 20px 0;
    width: 55%; 
    text-align: left;  /* 텍스트 왼쪽 정렬 */
    margin-left: -00px;  /* 텍스트를 훨씬 더 왼쪽으로 이동 */
}

.imgContainer {
    display: flex;
    justify-content: flex-start; /* 이미지 왼쪽 정렬 */
    gap: 15px; /* 이미지 간의 간격 */
    flex-wrap: wrap; /* 줄 바꿈 허용 */
    width: 55%; /* 부모 요소의 너비를 100%로 설정하여 왼쪽 정렬되도록 함 */
}

img {
    height: 40px; /* 이미지 크기 조정 */
    border-radius: 9px; /* 둥근 모서리 */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* 약간의 그림자 추가 */
    transition: transform 0.2s; /* 이미지에 마우스 올렸을 때 애니메이션 효과 추가 */
}

img:hover {
    transform: scale(1.1); /* 이미지에 마우스를 올리면 확대 효과 */
}