/* About.module.css */
.main {
    background-color: #060606;
    display: flex;
    flex-direction: column;
    justify-content: center;  /* 수직 중앙 정렬 */
    align-items: center;      /* 수평 중앙 정렬 */
    min-height: 100vh;        /* 화면 높이를 채우도록 설정 */
    padding: 20px;
    text-align: left;
    color: white;
    position: relative;  /* 자식 요소를 절대 위치로 설정할 수 있게 해줌 */
}

.title {
    position: absolute;  /* 절대 위치로 설정 */
    top: 100px;  /* 화면 상단에서 200px 아래로 위치 */
    left: 50%;  /* 화면 가로 가운데 */
    transform: translateX(-50%);  /* 정확히 가운데 정렬 */
    font-size: 60px;
    font-weight: bold;
    
}

.profileContainer {
    display: flex;
    align-items: center;  /* 수직 중앙 정렬 */
    justify-content: flex-start;  /* 사진을 왼쪽으로 이동 */
    margin-top: 60px;  /* 이미지와 텍스트 간의 간격을 넓힘 */
    max-width: 1000px;  /* 프로필 이미지와 텍스트가 나란히 배치될 공간 크기 설정 */
}

.profileImage {
    width: 280px;
    height: 280px;
    border-radius: 50%;  /* 동그랗게 만들기 */
    margin-right: 150px;  /* 이미지와 텍스트 사이의 간격을 더 좁힘 */
}

.textContainer {
    display: flex;
    flex-direction: column;  /* 텍스트를 세로로 정렬 */
    justify-content: flex-start;  /* 텍스트 수직 정렬 */
    max-width: 1000px;  /* 텍스트의 최대 너비를 확장 */
}

.maintext {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;  /* 텍스트 간격 설정 */
    align-items: flex-start;
}

.subtext {
    font-size: 17px;
    color: #555;
    line-height: 1.7;  /* 줄 간격을 넓혀서 읽기 좋게 설정 */
    margin-top: 30px;
    max-width: 2000px;  /* 텍스트의 최대 너비를 확장 */
    word-wrap: break-word;  /* 긴 단어가 넘칠 때 줄 바꿈 */
}
