/* MainScreen.module.css */
.mainScreen {
    background-image: url("../image/MainPhoto.jpg");
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.title {
    font-size: 50px; /* 타이틀 글씨 크기 50px */
    font-weight: bold;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    position: relative;
    animation: typing 3s steps(30) 1s 1 normal forwards; /* 타이핑 애니메이션 */
}


@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@keyframes blink {
    50% {
        background-color: transparent; /* 커서가 깜빡일 때 투명해짐 */
    }
}
