/* .main 스타일 */
.main {
    background-color: #060606;
    display: flex;
    flex-direction: column;
    justify-content: center;  /* 수직 중앙 정렬 */
    align-items: center;      /* 수평 중앙 정렬 */
    min-height: 100vh;        /* 화면 높이를 채우도록 설정 */
    padding: 20px;
    color: white;
}

/* .text 스타일 */
.text {
    font-size: 50px;
    font-weight: bold;
}

/* 기본 a 스타일 */
a {
    color: white; /* 기본 텍스트 색상 흰색 */
    text-decoration: none; /* 밑줄 제거 */
    display: inline-block; /* 인라인 블록으로 변경하여 transform 적용 가능하게 */
    transition: transform 0.2s ease, color 0.2s ease; /* 부드러운 애니메이션 */
}

/* 클릭 후에도 색상 유지 */
a:visited {
    color: white; /* 방문한 링크 색상도 흰색 */
}

/* 마우스 오버 시 텍스트 확대 및 색상 흰색 유지 */
a:hover {
    color: white; /* 마우스 오버 시 색상 흰색 */
    transform: scale(1.1); /* 텍스트 크기 확대 */
}

/* 클릭 상태에서도 색상 흰색 유지 */
a:active {
    color: white; /* 클릭 상태에서 색상 흰색 유지 */
}

.contact__lines {
    width: 100%;

    .line {
        display: block;
        width: 100%;
        height: 1px;
        background-color: var(--black);
        margin-bottom: 0.5vw;

        &:nth-child(1){
            height: 25px;
        }
        &:nth-child(2){
            height: 17px;
        }
        &:nth-child(3){
            height: 13px;
        }
        &:nth-child(4){
            height: 9px;
        }
        &:nth-child(5){
            height: 5px;
        }
        &:nth-child(6){
            height: 2px;
        }
        &:nth-child(7){
            height: 1px;
        }
    }

    &.top {
        .line {
            margin-top: 0.5vw;
            margin-bottom: 0;
            display: none;

            @media (max-width: 800px){ 
                display: block;
            }

            &:nth-child(1){
                height: 1px;
            }
            &:nth-child(2){
                height: 2px;
            }
            &:nth-child(3){
                height: 5px;
            }
            &:nth-child(4){
                height: 9px;
            }
            &:nth-child(5){
                height: 13px;
            }
            &:nth-child(6){
                height: 17px;
            }
            &:nth-child(7){
                height: 25px;
            }
        }
    }
}

